.control-panel {
    background: black;
    width: 640px;
    height: 360px;
}

.player {
    z-index: -1;
    position: absolute;
    top: 0;
    left: 0;
}

.controller {
    height: 100%;
}

.toolbar {
}

.toolbar .toolbar-component {
    display:inline;
    padding-left: 5px;
    padding-right: 5px;
}

.toolbar .toolbar-component span {
    font-size: 20px;
    cursor: pointer;
}

.playlist-tab {
    width: 100%;
    color: white;
}

table.song-list {
    color: white;
    width: 100%;
}

.control-panel thead {
    width: 100%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    -webkit-user-select: none;
    -moz-user-select: none;
    -o-user-select: none;
    -ms-user-select: none;
        user-select: none;
    text-align: -webkit-center;
}

.control-panel tbody {
    overflow-y: auto;
    width: 100%;
    height: 270px;
    text-align: left;
    -webkit-user-select: none;
    -moz-user-select: none;
    -o-user-select: none;
    -ms-user-select: none;
        user-select: none;
    display: block;
}

tbody td {
    width: 620px;
    display: -webkit-flex;
    display: flex;
}

tbody td div.title {
    -webkit-flex: 1 1;
            flex: 1 1;
}

.selected {
    color: white;
    background: gray;
}

.playing {
    color: white;
    background: darkred;
}

.song-control {
    margin-left: auto;
}

.my-btn {
    background: gray;
    padding: 5px;
    font-size: 15px;
    color: white;
    text-decoration: none;
    margin: 5px;
}

.my-btn:hover {
    background: gray;
    padding: 5px;
    font-size: 15px;
    color: white;
    text-decoration: none;
    cursor: pointer;
}

.btn:focus {
    outline: none !important;
    outline-offset: none !important;
    box-shadow: none;
}
